<template>
  <div>
    <div class="myspinner">
      <atom-spinner
        v-if="loading"
        :animation-duration="speed"
        :size="config.size"
        :color="spinnersColor"
      />
    </div>
    <div class="login_copy-header" style="padding-bottom: 13px; padding-left: 7px; position: relative; font-family: Montserrat;">
      <h2 class="font-weight--medium mt-20" style="font-size: 1.25rem; font-weight: 500; line-height: 1.5rem;">Login</h2>
    </div>
    <section id="form-register" style="display: block;">
      <div class="register no-form">
      <!-- OPTION IMAGE LINK -->
      <h2 style="font-size: 1.25rem; line-height: 1.5rem; font-weight: bold; margin-bottom: 10px;"><br>We are currently unable to register you on this platform.</h2>
                <p style="font-size: 0.875rem; line-height: 1.500625rem; font-weight: normal;">For internet package details and infrastructure installation, contact @MawinguNetworks on Twitter, MawinguNetworksLtd page on Facebook, the My Mawingu App or visit our offices at Cider Mall</p>
      </div>
    </section>
    <!-- <form @submit.prevent="showOTP ? submitFinal() : onsubmit()">
      <va-input
        v-focus
        v-if="!showOTP"
        v-model="email"
        type="text"
        :label="$t('auth.email-or-phone')"
        :placeholder="$t('auth.enter-email-or-phone')"
        :error="!!emailErrors.length"
        :error-messages="emailErrors"
      />

      <va-input
        v-if="!showOTP"
        v-model="password"
        type="password"
        :label="$t('auth.password')"
        :placeholder="$t('auth.enter-password')"
        :error="!!passwordErrors.length"
        :error-messages="passwordErrors"
      />

      <va-input
        v-if="showOTP"
        v-model="otp"
        type="text"
        :label="$t('auth.otp')"
        :error="!!otpErrors.length"
        :error-messages="otpErrors"
      />

      <div class="d-flex justify--center mt-3">
        <va-button type="submit" style="padding: 0.375rem 15.25rem; border-radius: 0.3rem; background-image: linear-gradient(to right, #4ab0e1, #4ab0e1);" class="my-0" :disabled="disableButton">{{ $t('auth.login') }}</va-button>
      </div>
    </form>
    <br>
    <div class="auth-layout__options d-flex align--center">
        Don't have an account yet? <router-link class="ml-1 link" :to="{name: 'signup'}">
          {{ $t('auth.createNewAccount') }} 
        </router-link>     
    </div>
    <div class="auth-layout__options d-flex align--center">
        Forgot Pin? <router-link class="ml-1 link" :to="{name: 'recover-password'}">
          {{$t('auth.recover_password')}} 
        </router-link>     
    </div> -->
  </div>
</template>

<script>
import axios from 'axios'
import * as spinners from 'epic-spinners'

export default {
  components: {
    ...spinners      
  },
  name: 'signup',
  data () {
    return {
      loading:false,
      disableButton:false,
      showOTP:false,
      form:{
        memb_no:'',
        password: '',
        confirmpassword:'',
        agreedToTerms: false,
        id_no : '',
        phone_no: '',
        otp:'',
      },      
      emailErrors: [],
      idNoErrors: [],
      phoneNoErrors: [],
      membNoErrors: [],
      passwordErrors: [],
      confirmPasswordErrors :[],
      agreedToTermsErrors: [],
      otpErrors: [],
      memberData:{},
      otp_no:'',
      config: {
        size: 80,
        duration: 1500,
      },
      spinnersColor: "#df902a",
      currentDuration: 1500,
      sliderDuration: {
        min: 1000,
        max: 2000,
      },
    }
  },
  computed: {
    speed () {
      return this.sliderDuration.min + this.sliderDuration.max - this.currentDuration
    },
    formReady () {
      return !this.membNoErrors.length  && !this.passwordErrors.length  && !this.idNoErrors.length  && !this.phoneNoErrors.length  && !this.confirmPasswordErrors.length  && !this.agreedToTermsErrors.length && !this.otpErrors.length
    },
  },
  methods: {
    checklength(){
      this.passwordErrors = this.form.password.length != 4 ? ['Enter four digit PIN'] : []
      if(!this.passwordErrors.length){
        this.passwordErrors = /^\d+$/.test(this.form.password) ? [] : ['Pin should countain digits only']
      }     
    },
    onsubmit () {
      this.membNoErrors = this.form.memb_no ? [] : ['Member No. field is required']
      this.passwordErrors = this.form.password ? [] : ['PIN is required']
      this.idNoErrors = this.form.id_no ? [] : ['ID No. is required']
      this.phoneNoErrors = this.form.phone_no ? [] : ['Phone No. is required']
      this.confirmPasswordErrors = this.form.confirmpassword ? [] : ['Confirm PIN field is required']
      this.agreedToTermsErrors = this.form.agreedToTerms ? [] : ['You must agree to the terms of use to continue']

      if (this.form.password != this.form.confirmpassword) {        
        this.passwordErrors = ['PINs Do not Match']
        this.confirmPasswordErrors =  ['PINs Do not Match']
      }      

      if (this.formReady) { 
        var self = this 
        self.loading = true;  
        self.disableButton = true;   
        axios.post(`${this.$apiUrl}RegUser/55`, this.form)
          .then(function (res) {
            self.loading = false;
            self.disableButton = false;  
            const response = res.data

            self.memberData = response.db_array;
            self.otp_no = response.otp;

            if (response.success === 1) {
              self.showOTP = true;
              self.otpErrors = ['Input OTP']
              self.showToast(`Successful! An OTP has been send to your Phone NO. Please input 4 digit OTP to complete application process`, {
                position: 'bottom-right',
                icon: "fa-check",
                duration: 10000,
              })
              setTimeout( () => self.showOTP = false, 300000);
            }

            if (response.success === 2) {
              self.membNoErrors = ['You are already a registered Portal or Mobile user']
              self.showToast(`You are already a registered Portal or Mobile user, use your mobile banking credentials to login or dial +254715114454 for PIN Reset assistance.`, {
                position: 'bottom-right',
                icon: "fa-times",
                duration: 300000,
              })
            } 

            if (response.success === 3) {
              self.membNoErrors = ['Account Not Found!']
              self.idNoErrors = ['Account Not Found!']
              self.phoneNoErrors = ['Account Not Found!']
              self.showToast(`Account Not Found! Confirm Your Member No, ID No, and Phone NO`, {
                position: 'bottom-right',
                icon: "fa-times",
                duration: 10000,
              })
            }

          })
          .catch(function (error) {
            console.log(error)
        })
      }
    },

    submitFinal(){
      this.otpErrors = this.form.otp ? [] : ['OTP field field is required']
      if (this.form.otp != this.otp_no) {
        this.otpErrors = ['OTP does not match the one sent']
      }
      if (this.formReady) {

        const finalData = {
          formdata: this.form,
          db_array: this.memberData
        }

        var self = this  
        self.loading = true;
        self.disableButton = true;      
        axios.post(`${this.$apiUrl}RegUserFinal`, finalData)
        .then(function (res) {
          const response = res.data
          self.loading = false;
          self.disableButton = false;  
          if (response.success === 1) { 
            self.showToast(`Successful! Your application has been recieved you will recieve an sms confirmation once approved`, {
                position: 'bottom-right',
                icon: "fa-check",
                duration: 2500,
            })
            self.$router.push({name: "login" }); 
          }
        })
        .catch(function (error) {
            console.log(error)
        })
      }
    }
  }
}
</script>

<style lang="scss">
.myspinner {
  position: fixed;
  top: 40%;
  left: 22%;
  z-index: 999;
}

section {
  padding-left: 1px;
}

@media (max-width: 767px) {
  .myspinner {
    position: fixed;
    top: 38%;
    left: 40%;
    z-index: 999;
  }
}
</style>
